<template>
  <div class="col s12 m6 l4">
    <div class="card light-blue bill-card">
      <div class="card-content white-text">
        <span class="card-title">Счет в валюте</span>

        <p v-for="cur of currencies" :key="cur" class="currency-line">
          <span>{{ baseCurrency * currencies.get(cur[0]) }} {{ cur[0] }}</span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    // currencies: ["RUB", "USD", "EUR"],
    currencies: new Map([
      ["RUB", 77.023894],
      ["USD", 1.121969],
      ["EUR", 1]
    ])
  }),
  computed: {
    baseCurrency() {
      return this.$store.getters.info.bill / (77.023894 / 1);
    }
  }
};
</script>
